
#grid {
  position: absolute;
  width: 400px;
  height: 400px;
  left: 50%;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  border-radius: 30px;
  border: 2.5mm ridge #aa32dc;
  /* DAMIER */
  background-color: #86f89f;
  background-image: linear-gradient(45deg, #4cfa72 25%, transparent 25%, transparent 75%, #4cfa72 75%), linear-gradient(45deg, #4cfa72 25%, transparent 25%, transparent 75%, #4cfa72 75%);
  background-size: 100px 100px;
  background-position: 0 0, 50px 50px;
}



